import Image from "next/image";
import { useState } from "react";
import { FaSearch, FaUser, FaStar, FaShoppingCart } from "react-icons/fa";

const links = ["Men", "Women", "Essentials", "LookBooks"];

export default function Navbar() {
  const [activeLink, setActiveLink] = useState("Men");

  return (
    <nav className="w-full bg-white shadow-md">
      <div className="max-w-7xl mx-auto flex items-center justify-between px-6 py-4">
        
        {/* Left Links */}
        <div className="flex space-x-6">
          {links.map((link) => (
            <button
              key={link}
              onClick={() => setActiveLink(link)}
              className={`relative font-medium ${
                activeLink === link ? "border-b-2 border-black" : ""
              }`}
            >
              {link}
            </button>
          ))}
        </div>

        {/* Brand Name */}
        <div className="font-bold italic text-xl text-center">
          PEGADOR
        </div>

        {/* Right Icons */}
        <div className="flex items-center space-x-4">
          <FaSearch className="text-lg cursor-pointer" />
          <Image
            src="/flags/pakistan.png"
            alt="Pakistan"
            width={24}
            height={24}
            className="rounded-full"
          />
          <FaUser className="text-lg cursor-pointer" />
          <FaStar className="text-lg cursor-pointer" />
          <FaShoppingCart className="text-lg cursor-pointer" />
        </div>
      </div>
    </nav>
  );
}
